import React from 'react';
import './App.css';

const Button = function(props) {
    const [state, setter] = React.useState(0); 
    return (
        <div>
            <button onClick={()=>{setter(state+1)}}>{state}</button>
        </div>
    );
}

const List = function(props) {
    const [list, setlist] = React.useState([]);
    const [item, setItem] = React.useState("");

    React.useEffect(()=>{
        const keylistener = (e) => {
            if(e.isComposing || e.keyCode === 229) return ;
            else if(e.code === "Enter"){
                setlist([...list, item]);
            }
        }
        window.addEventListener('keydown',keylistener);
        return () => { window.removeEventListener('keydown', keylistener)};
    })
    
    return (
        <div className="App">
            <h2>List</h2>
            <ul style={{width:"300px", margin:"auto"}}>
                {list.map((item, idx) => <li key={idx}>{item}</li>)}
            </ul>
            <div style={{marginTop: "30px"}}>
                <label htmlFor="item">Item: </label>
                <input id="item" type="text" value={item} onChange={e => setItem(e.target.value)}   />
                <button onClick={()=>(setlist([...list, item]))}>Add</button>
            </div>
        </div>
    )
}

export default {List, Button};


